<template>
	<div class="detail-total">
		<h4 class="total-title">
			<span>共{{productArr.length}}条</span>
			<span class="sright">合计</span>
			<div class="clear"></div>
		</h4>
		<div class="total-box">
			<div class="total-item">
				<div class="allnum">
					总数量
					<span class="s-blue">{{allNum}}</span>
				</div>
				<div class="allnum">
					已执行<span class="s-red">{{allExecuted}}</span>
				</div>
			</div>
			<div class="total-item">
				<div class="allnum allmoney">总金额:￥{{allMoney}}</div>
			</div>
		</div>
	</div>
</template>

<script>

export default {
  name: 'OperateStockTotal',
  components:{
  },
  props:{
	productList:{
		type:Array,
		default(){
			return []
		}
	}
  },
  data(){
	return{
		
	}
  },
  methods:{
	//计算总值
	MathcAllNum(key){
		let res=0;
		this.productList.forEach(obj=>{
			res+=parseFloat(obj[key])
		})
		return res
	},
	//计算总金额
	MathcAllMoney(){
		let res=0;
		this.productList.forEach(obj=>{
			res+=parseFloat(obj.price)*parseFloat(obj.num)
		})
		return res
	},
  },
  computed:{
	productArr(){
		return this.productList
	},
	//总金额
	allMoney(){
		return this.MathcAllMoney().toFixed(2);		//取.后两位四舍五入
		
	},
	//总数量
	allNum(){
		return this.MathcAllNum('num')
	},
	//总执行数
	allExecuted(){
		return this.MathcAllNum('num')
	}
  },
}
</script>


